@import url(https://fonts.googleapis.com/css?family=Poppins:500,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.header {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(2);
  font-family: "Poppins", sans-serif; }
  .header__name {
    color: #35323e;
    font-weight: 700;
    grid-column: 2/3; }
  .header__name:hover {
    color: #232127;
    cursor: pointer; }
  .header__menu {
    display: none; }
  .header__navbar {
    grid-column: 3/12;
    display: flex;
    flex-direction: row;
    align-items: center; }
    .header__navbar__link {
      color: #9e9aa7;
      font-weight: 700;
      font-size: 16px;
      margin: 0 15px; }
      .header__navbar__link--right {
        margin-left: auto;
        margin-right: 10px; }
    .header__navbar__link:hover, .header__navbar__link--right:hover {
      color: #232127;
      cursor: pointer; }
    .header__navbar__signup {
      background-color: #2acfcf;
      padding: 10px 15px;
      border: 1px #51dbdb;
      color: white;
      font-size: 16px;
      border-radius: 20px;
      font-weight: 700;
      height: 40px; }

@media (max-width: 678px) {
  .header__navbar {
    flex-direction: column;
    grid-column-start: 2;
    align-items: center;
    margin-bottom: 30px; }
  .header__navbar__link,
  .header__navbar__link--right {
    margin: 7px 0; }
  .Navbar__Items {
    display: none; }
  .Navbar__ToggleShow {
    display: flex; }
  .header__menu {
    grid-column-end: 12;
    display: inline;
    display: initial;
    cursor: pointer;
    margin: 25px 0; }
  .header__navbar__signup {
    padding: 10px 60px;
    font-size: 16px; } }

.Intro {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  text-align: left;
  font-family: "Poppins", sans-serif;
  padding-bottom: 30px; }
  .Intro__body {
    grid-column: 2/7;
    display: flex;
    flex-direction: column;
    justify-content: center; }
  .Intro__title {
    color: #35323e;
    font-weight: 700;
    font-size: 38px;
    margin: 0; }
  .Intro__text {
    color: #9e9aa7;
    font-size: 14px;
    margin-top: 0; }
  .Intro__button {
    background-color: #2acfcf;
    padding: 10px 15px;
    border: 1px #51dbdb;
    color: white;
    font-size: 14px;
    border-radius: 20px;
    font-weight: 700;
    height: 40px;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content; }
  .Intro__img {
    grid-column: 8/-1;
    overflow: hidden;
    margin: 0; }
  .Intro__img > img {
    width: 115%;
    object-fit: cover;
    padding: 10px; }

@media (max-width: 588px) {
  .Intro {
    grid-template-rows: repeat(2, auto);
    text-align: center; }
    .Intro__body {
      grid-column: 2/12;
      grid-row: 2/3;
      align-items: center; }
  .Intro__img {
    grid-column: 2/-1;
    grid-row: 1/2;
    overflow: hidden;
    margin: 0; }
  .Intro__img > img {
    width: 130%;
    padding: 20px;
    object-fit: cover; }
  .Intro__button {
    padding: 10px 25px; } }

.container {
  display: flex;
  align-items: center;
  flex-direction: column;
  background-color: #edecee; }

.search {
  width: 80%;
  height: 130px;
  background: #232127 url(/Shortly/static/media/bg-shorten-desktop.a82de60e.svg) top center repeat;
  border-radius: 6px;
  display: flex;
  justify-content: space-evenly;
  align-items: center; }
  .search__input {
    width: 70%;
    height: 30px;
    color: #9e9aa7;
    font-size: 14px;
    border-radius: 5px;
    padding: 5px;
    border: none; }
  .search__button {
    height: 40px;
    color: white;
    font-size: 14px;
    border-radius: 5px;
    border: none;
    padding: 0 20px;
    background-color: #2acfcf;
    font-weight: 700; }

.item {
  width: 77%;
  background-color: white;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-top: 5px;
  border-radius: 5px;
  font-size: 13px;
  font-weight: 700;
  padding: 0 10px; }
  .item__name {
    color: #232127;
    text-align: left;
    word-break: break-all; }
  .item__row {
    display: flex;
    flex-direction: row;
    padding-right: 20px;
    justify-content: space-between;
    width: 100%; }
  .item__link {
    color: #2acfcf;
    text-align: right;
    word-break: break-all; }
  .item__copy {
    background-color: #2acfcf;
    color: white;
    border: 0;
    font-size: 12px;
    font-weight: 700;
    padding: 0 20px;
    height: 30px;
    border-radius: 5px; }
  .item__copy:active {
    background-color: #a2ecec; }

@media (max-width: 590px) {
  .search {
    flex-direction: column; }
    .search__input {
      width: 76%;
      padding: 7px; }
    .search__button {
      width: 80%; } }

@media (max-width: 613px) {
  .item {
    font-size: 12px;
    font-weight: 400;
    padding: 8px; }
    .item__row {
      padding-right: 5px;
      flex-direction: column; }
    .item__link, .item__name {
      text-align: left;
      margin: 5px 0 0 0; }
    .item__copy {
      font-size: 10px;
      padding: 0 10px; } }

.container {
  display: flex;
  align-content: center;
  padding-bottom: 30px; }

.title {
  font-size: 24px;
  font-weight: 700;
  color: #232127;
  margin-bottom: 0; }

.subtitle {
  font-size: 12px;
  font-weight: 400;
  color: #9e9aa7;
  width: 45%; }

.boxes {
  display: flex;
  flex-direction: row;
  align-content: center;
  justify-content: center;
  margin-top: 40px; }

.line {
  background-color: #51dbdb;
  width: 25px;
  height: 7px;
  display: flex;
  align-self: center; }

@media (max-width: 560px) {
  .boxes {
    display: flex;
    flex-direction: column;
    align-content: center; }
  .line {
    width: 7px;
    height: 60px; } }

.box-container {
  position: relative;
  width: 25%;
  text-align: left; }

.box {
  padding: 20px; }
  .box__circle {
    position: absolute;
    top: -25px;
    left: 20px;
    background-color: #3b3054;
    width: 55px;
    height: 55px;
    border-radius: 30px;
    display: flex;
    align-items: center;
    justify-content: center; }
  .box__img {
    width: 30px;
    margin-bottom: 5px; }
  .box__body {
    background-color: white;
    padding: 30px 15px 15px 15px; }
    .box__body__title {
      font-size: 16px;
      font-weight: 700;
      color: #232127;
      margin-bottom: 0; }
    .box__body__desc {
      font-size: 12px;
      font-weight: 400;
      color: #9e9aa7; }

.box2 {
  margin-top: 30px; }

.box3 {
  margin-top: 60px; }

@media (max-width: 560px) {
  .box3,
  .box2 {
    margin-top: 0; }
  .box-container {
    position: relative;
    width: 70%;
    display: flex;
    align-self: center;
    text-align: center; }
  .box__circle {
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0; } }

.footer {
  background-color: #35323e;
  color: white;
  display: grid;
  grid-template-columns: repeat(9, 1fr);
  grid-template-rows: 100%;
  grid-column-gap: 20px;
  grid-row-gap: 30px;
  text-align: left;
  padding: 3% 20px; }
  .footer__name {
    grid-column: 2/3;
    margin-top: 0;
    font-size: 28px;
    font-weight: 700; }
  .footer__features {
    grid-column: 4/5; }
  .footer__title {
    margin-top: 0;
    color: white;
    font-weight: 700;
    font-size: 14px; }
  .footer__link {
    margin-top: 0;
    color: #9e9aa7;
    font-weight: 400;
    cursor: pointer;
    font-size: 13px; }
  .footer__link:hover {
    color: #51dbdb; }
  .footer__icons {
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    grid-column: 8/9; }
    .footer__icons svg {
      fill: white;
      padding: 0 7px; }
  .footer svg:hover {
    fill: #51dbdb; }

@media (max-width: 667px) {
  .footer {
    grid-template-columns: auto;
    grid-template-rows: auto;
    grid-row-gap: 30px; }
    .footer__name, .footer__features, .footer__resources, .footer__company {
      grid-column: 1;
      display: flex;
      flex-direction: column;
      align-items: center; }
    .footer__icons {
      grid-column: 1;
      display: flex;
      flex-direction: row;
      width: 100%;
      justify-content: center;
      padding-bottom: 20px; } }

